.photo img {
  width: 100%;
}
.photo {
  position: relative;
  overflow: hidden;
}
.photo p {
  position: absolute;
  top: 40%;
  left: 100%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 100%;
  text-align: center;
  color: white;
  -webkit-animation: p1 15s linear infinite;
          animation: p1 15s linear infinite;
  font-family: 'Lato', sans-serif;
  font-size: 18px;
}
.photo p span {
  position: relative;
  padding-bottom: 3px;
}
.photo p span::after {
  content: "";
  position: absolute;
  width: 80%;
  left: 10%;
  bottom: 0;
  height: 1px;
  background-color: rgb(118, 190, 118);
}
.photo p:nth-child(1) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.photo p:nth-child(2) {
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
.photo p:nth-child(3) {
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
}
.photo a {
  position: absolute;
  top: 65%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: inline-block;
  background-color: rgb(70, 114, 42);
  text-align: center;
  color: white;
  font-family: 'Lato', sans-serif;
  text-decoration: none;
  padding: 5px 13px;
  font-size: 12px;
  border: 2px solid rgb(70, 114, 42);
  border-radius: 3px;
  transition: .2s;
}
.photo a:hover{
  color:rgb(70, 114, 42);
  background-color: transparent;
}
@-webkit-keyframes p1 {
  0% {
    left: 100%;
  }
  15% {
    left: 0;
  }
  30% {
    left: 0;
    opacity: 1;
  }
  35%{
    left: 0;
    opacity: 0;
  }
  100%{
    left: 0;
    opacity: 0;
  }
}
@keyframes p1 {
  0% {
    left: 100%;
  }
  15% {
    left: 0;
  }
  30% {
    left: 0;
    opacity: 1;
  }
  35%{
    left: 0;
    opacity: 0;
  }
  100%{
    left: 0;
    opacity: 0;
  }
}
@media(min-width: 640px){
  .photo p {
    font-size: 22px;
  }
